<template>
  <div>
    <div class="component_example">
      <h1>v-keyfocus 快捷键的使用</h1>
      <h2>常用的操作</h2>
      <div class="item">
        <h4>基本用法</h4>
        <div class="item-content" v-keyfocus="focusObj" :data-current="current" tabindex="-1">
          <zoehis-button ref="keyfocus1" @focus="current=1">按钮</zoehis-button>
          <zoehis-checkbox v-model="checked" ref="keyfocus2" @focus="current=2" disabled>复选框</zoehis-checkbox>
          <zoehis-input disabled v-model="input1" ref="keyfocus3" @focus="current=3" placeholder="单行文本框"></zoehis-input>
          <zoehis-input type="textarea" v-model="textarea1" ref="keyfocus4" @focus="current=4" placeholder="多行文本框"></zoehis-input>
          <zoehis-select v-model="select1V" :selectdata="select1data" ref="keyfocus5" @focus="current=5" disabled></zoehis-select>
          <zoehis-time-picker ref="keyfocus6" v-model="time1v" @focus="current=6" disabled></zoehis-time-picker>
          <input type="text" @focus="current=7" ref="keyfocus7" placeholder="原始单行文本框">
          <textarea @focus="current=8" ref="keyfocus8" placeholder="原始单行文本框"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>


<script type="text/ecmascript-6">
  import keyfocus from '../../zoehisVue/src/utils/keyfocus.js'
  export default{
    name:'keyfocus1',
    directives:{keyfocus},
    data: function () {
      return {
        test1:"1",
        current:-1,
        checked:true,
        input1:"",
        textarea1:"",
        time1v:"",
        select1V:1,
        select1data:[
          {id:1,text:"龙虾"},
          {id:2,text:"大龙虾"},
          {id:3,text:"螃蟹"}
        ],
        focusObj:{
          keyRefs:[
            {keyIndex:1,ref:'keyfocus1'},
            {keyIndex:2,ref:'keyfocus2'},
            {keyIndex:3,ref:'keyfocus3'},
            {keyIndex:4,ref:'keyfocus4'},
            {keyIndex:5,ref:'keyfocus5'},
            {keyIndex:6,ref:'keyfocus6'},
            {keyIndex:7,ref:'keyfocus7'},
            {keyIndex:8,ref:'keyfocus8'},
//            {keyIndex:9,ref:'keyfocus9'},
          ],
          onBeforeNext(current){
            console.log("current",current)
          },
          onAfterPrev(){},
          onLastNext(){},
          onFirstPrev(){},
        }
      };
    },
    mounted:function() { //用于高亮显示代码
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {

    }
  }
</script>



