<template>
  <div
    class="zoehis_select_dropdown"
    :style="{minWidth:minWidth}"
  >
    <slot></slot>
  </div>
</template>

<script type="text/babel">
  import Popper from '../../../src/utils/vue-popper';

  export default {
    name: 'ZoehisSelectDropdown',

    componentName: 'ZoehisSelectDropdown',

    mixins: [Popper],

    props: {
      placement: {
        default: 'bottom-start'
      },

      boundariesPadding: {
        default: 0
      },

      popperOptions: {
        default() {
          return {
            forceAbsolute: true,
            gpuAcceleration: false
          };
        }
      }
    },

    data() {
      return {
        minWidth: ''
      };
    },

    computed: {
      popperClass() {
        return this.$parent.popperClass; //父组件的popperClass属性
      }
    },

    watch: {
      '$parent.inputWidth'() {
        //获取输入框的宽度，getBoundingClientRect会返回这六个属性top,lef,right,bottom,width,height
        this.minWidth = this.$parent.$el.getBoundingClientRect().width + 'px';
      }
    },

    mounted() {
      this.referenceElm = this.$parent.$refs.reference.$el; //父组件的input输入框的dom元素
      this.$parent.popperElm = this.popperElm = this.$el;
      this.$on('updatePopper', () => {
        if (this.$parent.visible) this.updatePopper();
      });
      this.$on('destroyPopper', this.destroyPopper);
    }
  };
</script>



