<template>
  <div
    :class="[
      type === 'textarea' ? 'zoehis_textarea' : 'zoehis_input',
      {
        'zoehis_over':(mouseover || isFocus),
        'zoehis_disabled':disabled,
        'zoehis_line':isShowLine,
      }
    ]"
    @click="handleClick"
    :style="textStyle"
  >
    <i
      v-if="showIcon"
      class="zoeIconfont"
      :class="iconClass"
      @click.stop="handleClickIcon"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
    ></i>
    <input
      v-if="type!=='textarea'"
      class="zoehis_input_inner"
      type="text"
      ref="input"
      :placeholder="isShowLine ? '' : placeholder"
      :disabled="disabled"
      :readonly="readonly"
      v-on="{focus:handleFocus,blur:handleBlur}"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
      @input="handleInput"
      @keyup.46="handleClear"
      v-model="currentValue"

    />
    <textarea
      v-else
      class="zoehis_textarea_inner"
      type="textarea"
      ref="textarea"
      :placeholder="disabled ? '' : placeholder"
      :disabled="disabled"
      :readonly="readonly"
      v-on="{focus:handleFocus,blur:handleBlur}"
      @mouseover="mouseover = true"
      @mouseout="mouseover = false"
      @input="handleInput"
      @keyup.46="handleClear"
      v-model="currentValue"
    ></textarea>
  </div>
</template>
<script>
  export default {
    name: 'ZoehisInput',
    componentName: 'ZoehisInput',
    props: {
      type: {    //文本类型
        type: String,
        default: "text"
      },
      value: {
        default:""
      }, //定义父组件传过来的值，即v-model
      icon: String,
      disabled: Boolean,
      readonly: Boolean,//是否只读，true：只读，false不是
      close: {      //是否显示关闭按钮
        type: Boolean,
        default: false
      },
      clearreadonly: {      //只读状态是否可以清空数据
        type: Boolean,
        default: false
      },
      placeholder: String,
      width: {      //文本的宽度
        type:[Number,String],
        default: 115
      },
      height: {      //文本的height
        type:[Number,String],
        default: ""
      }
    },
    data(){
      return{
        currentValue:"",//当前的值
        mouseover:false,//用来判断鼠标悬浮的
        isFocus:false,//焦点聚集状态
      }
    },
    computed:{
      isShowLine:function(){
        return (this.disabled && !this.currentValue) ? true : false;
      },
      textStyle(){
        var re = /^[0-9]+\.?[0-9]*$/; //匹配是否是数字
        return {
          width: re.test(this.width) ? this.width+'px' : this.width,
          height: re.test(this.height) ? this.height+'px' : this.height
        }
      },
      showIcon(){
        //close关闭按钮（只有在只读状态能够清空的情况下才显示）
        if((this.close && !this.disabled) || (this.icon && !this.disabled) || this.type === 'textarea'){
          return true;
        }else {
          return false;
        }
      },
      iconClass(){
        let html = '';
        if(this.type === 'textarea'){
          html = 'zoehis_textarea_icon z_close_normal'
        }else {
          html = 'zoehis_input_icon ';
          if(this.close){
            html += 'zoehis_close z_close_normal';
          }else if(this.icon){
            html += this.icon;
          }
        }
        return html;
      }
    },
    watch: {
      value(val) {
        this.setValue(val);
      }
    },
    methods: {
      //获取是否可以编辑的状态
      getDisabled(){
        return this.disabled;
      },
      getValue:function(){ //获取当前的值（给父组件用）
        return this.currentValue;
      },
      setValue:function(val){ //设置值（给父组件用）
        if(val===this.currentValue) return;
        this.currentValue=val;
        this.$emit('input', val);
      },
      handleFocus(event) {//焦点聚集事件
        this.isFocus=true;
        this.$emit('focus', event);
      },
      handleBlur(event) {//焦点离开事件
        this.isFocus=false;
        this.$emit('blur', event);
      },
      handleInput(event) {//值改变事件
        const value = event.target.value;
        this.$emit('input', value);
        this.$emit('change', value);
      },
      handleClickIcon(event) {
        if(this.close || this.type==='textarea'){
          this.handleClear(event);
        }else {
          this.$emit('clickicon', event);
        }
      },
      handleClear(event) { //清空事件
        let noClear = this.currentValue=="" ||
          (!this.clearreadonly && this.readonly) ||
          this.disabled ||
          (!this.close && this.type!=='textarea');
        if(noClear) return;
        this.$emit('input', "");
        this.$emit('change', "");
        this.$emit('clear', event);
      },
      handleClick(event) { //点击输入框的事件
        this.focus();
        this.$emit('click', event);
      },
      //全选方法
      inputSelect() {
        this.$refs.input.select();
      },
      //焦点聚集方法
      focus() {
        this.$refs[this.type!='textarea' ? 'input' : 'textarea'].focus();
      }
    },
    created() {
      this.currentValue=this.value;
      //监听全选事件
      this.$on('inputSelect', this.inputSelect);
    }
  };
</script>



