<template>
  <label
    class="zoehis_checkbox"
    :class="isFocus ? 'zoehis_focus' : ''"
  >
    <span
      class="zoehis_checkbox_input zoeIconfont"
      :class="[
      isChecked ? 'z_checkboxS_form' : 'z_checkbox_form',
      {
        'zoehis_disabled':disabled
      }]"
    >
      <input
        type="checkbox"
        class="zoehis_checkbox_original"
        :disabled="disabled"
        @focus="handleFocus"
        @blur="isFocus = false"
        @keyup.enter="handleEnter"
        :name="name"
        :value="label"
        v-model="model"
        ref="input"
      >
    </span>
    <span v-if="$slots.default || label">
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  import Emitter from '../../../src/mixins/emitter.js';
  export default {
    name: 'ZoehisCheckbox',
    componentName: 'ZoehisCheckbox',
    mixins: [Emitter],
    props: {
      value: {},
      label: {},
      disabled:Boolean,//是否可用
      checked: Boolean,//选中的状态
      name: String
    },
    data(){
      return{
        isFocus:false,         //焦点聚集
        selfModel: false,
      }
    },
    computed:{
      model: {
        get() {
          return this.isGroup
            ? this.store : this.value !== undefined
            ? this.value : this.selfModel;
        },
        set(val) {
          if (this.isGroup) {
            this.dispatch('ZoehisCheckboxGroup', 'input', [val]);//向父组件触发事件设置值
          } else {
            this.$emit('input', val);
            this.selfModel = val;
          }
        }
      },
      isGroup() {//判断是不是复选框的组
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ZoehisCheckboxGroup') {
            parent = parent.$parent;
          } else {
            this._checkboxGroup = parent;
            return true;
          }
        }
        return false;
      },
      isChecked() {//是否选中
        if ({}.toString.call(this.model) === '[object Boolean]') {//判断model是不是Boolean类型
          return this.model;
        } else if (Array.isArray(this.model)) {//数组类型
          return this.model.indexOf(this.label) > -1;
        }
      },
      store() {//获取值
        return this._checkboxGroup ? this._checkboxGroup.value : this.value;
      }
    },
    methods: {
      //获取是否可以编辑的状态
      getDisabled(){
        return this.disabled;
      },
      focus:function(){//焦点定位方法
        this.$refs.input.focus();
      },
      handleEnter:function(evt){//enter事件
        this.$emit("enter",evt);
      },
      //焦点聚集的方法
      handleFocus(){
        this.isFocus = true;
        this.$emit("focus");
      },
      addToStore() {//设置初始化
        if (
          Array.isArray(this.model) &&
          this.model.indexOf(this.label) === -1
        ) {
          this.model.push(this.label);
        }else {
          this.model=true;
        }
      }
    },
    created(){
      this.checked && this.addToStore();
    }
  };
</script>



